body {
  margin: 0;
}

form {
  background: #5757e9;
  justify-content: center;
  align-items: center;
  padding: 20px;
  display: flex;
}

input {
  width: 400px;
  height: 25px;
  border: none;
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
  outline: none;
  padding: 3px;
}

button[type="submit"] {
  height: 31px;
  width: 50px;
  border: none;
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
  outline: none;
  transition: background .25s linear;
}

button[type="submit"]:hover {
  background: #b5b4b4;
}

.gallery {
  flex-flow: wrap;
  align-items: center;
  gap: 20px;
  padding: 20px;
  display: flex;
}

.photo-card {
  width: calc(25% - 20px);
  flex-direction: column;
  display: flex;
}

.photo-card img {
  height: 400px;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
}

.photo-card .info {
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
  justify-content: space-around;
  display: flex;
  box-shadow: 0 5px 5px #000;
}

.info-item {
  flex-direction: column;
  justify-content: space-between;
  margin-top: 5px;
  display: flex;
}

